.top {
    width: 100%;
    height: 50px;
    background-color: rgb(255, 255, 255, 0.5);
    backdrop-filter: blur(10px);
    position: sticky;
    top: 0;
    display: flex;
    align-items: center;
    z-index: 999;
    font-family: "Josefin Sans", sans-serif;
  }
  
  .topLeft,
  .topRight {
    flex: 3;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .topCenter {
    flex: 6;
  }

  .topIcon{
    font-size: 20px;
    margin-right: 10px;
    color: #444;
    cursor: pointer;
    transition: all 0.2s;
  }
  .topIcon:hover{
    color: rgb(0, 0, 0);
    transition: all 0.2s;

  }
  .topImg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
  }

  .topList{
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style: none;
    color: #444;
    border-bottom: 1px solid #ffffff00;
  }
  .topList >li:hover{
    color: rgb(0, 0, 0);
    border-bottom: 1px solid #444;
    transition: all 0.3s;
  }
  .topListItem{
    margin-right: 20px;
    font-size: 20px;
    font-weight: 300;
    cursor: pointer;
  }

  .topSearchIcon{
    font-size: 18px;
    color: #666;
    cursor: pointer;
    margin-left: 15px;
  }
 